import React, { Fragment, useState, useEffect } from 'react';
import {
  EuiFlexGroup,
  EuiFlexItem,
  EuiFieldSearch,
  EuiBasicTable,
  EuiTextColor,
  EuiHealth,
  EuiAvatar,
  EuiImage,
  EuiSuperSelect,
  EuiButton,
  EuiSpacer,
  EuiPanel,
  EuiStat,
  EuiIcon
} from '@elastic/eui';
import AV from 'leancloud-storage';
import ac from 'accounting';
import _ from 'lodash';
import styles from './index.module.scss';
import { StockDetail } from './components';

const Stock = props => {
  const [statLoading, setStatLoading] = useState(true);
  const [stockDetailVisible, setStockDetailVisible] = useState(false);
  const [selectedStockId, setSelectedStockId] = useState('');
  const [statInfo, setStatInfo] = useState({
    allNumber: 0,
    allTotal: 0
  });
  const [items, setItems] = useState([]);
  const [sizeOptions, setSizeOptions] = useState([]);
  const [colorOptions, setColorOptions] = useState([]);
  const [keywords, setKeywords] = useState('');
  const [pageIndex, setPageIndex] = useState(0);
  const [pageSize, setPageSize] = useState(10);
  const [total, setTotal] = useState(0);
  const queryStat = async () => {
    const allStockQuery = new AV.Query('Stock');
    allStockQuery.include('Goods');
    allStockQuery.limit(1000);
    const allStock = await allStockQuery.find();
    const statInfo = allStock.reduce(
      (a, b) => ({
        allNumber: a.allNumber + b.get('number'),
        allTotal: a.allTotal + b.get('total')
      }),
      {
        allNumber: 0,
        allTotal: 0
      }
    );
    setStatInfo(statInfo);
    setStatLoading(false);
  };
  const queryDict = async () => {
    const colorQuery = new AV.Query('Color');
    const sizeQuery = new AV.Query('Size');
    let colors = await colorQuery.limit(1000).find();
    let sizes = await sizeQuery.limit(1000).find();
    const colorOptions = colors.map(c => ({
      value: c.get('hex'),
      inputDisplay: (
        <EuiHealth color={'#' + c.get('hex')}>{c.get('name')}</EuiHealth>
      )
    }));
    const sizeOptions = sizes.map(s => ({
      value: s.get('value'),
      inputDisplay: s.get('name'),
      dropdownDisplay: <strong>{s.get('name')}</strong>
    }));

    setColorOptions(colorOptions);
    setSizeOptions(sizeOptions);
  };
  const queryStocks = async (
    key = keywords,
    index = pageIndex,
    size = pageSize
  ) => {
    const query = new AV.Query('Stock');
    query.descending('updatedAt');
    const goodsQuery = new AV.Query('Goods');
    goodsQuery.contains('name', key);
    query.include('goods');
    query.matchesQuery('goods', goodsQuery);
    const total = await query.count();
    const stocks = await query
      .skip(index * size)
      .limit(size)
      .find();
    setTotal(total);
    setItems(stocks);
    setKeywords(key);
    setPageIndex(index);
    setPageSize(size);
  };
  const onTableChange = change => {
    const { page, sort } = change;
    if (page) {
      const { index, size } = page;
      queryStocks(keywords, index, size);
    }
  };
  const onSearchChange = key => queryStocks(key, 0);
  const debouncedOnSearchChange = _.debounce(onSearchChange, 500);
  const pagination = {
    pageIndex,
    pageSize,
    totalItemCount: total,
    pageSizeOptions: [5, 10, 20]
  };
  const columns = [
    {
      name: '',
      field: 'goods.picture',
      render: (val, item) => {
        return (
          <EuiImage
            size={100}
            hasShadow
            allowFullScreen
            alt={item.get('goods').get('name')}
            url={item
              .get('goods')
              .get('picture')
              .get('url')}
          />
        );
      }
    },
    {
      name: '',
      field: 'goods.name',
      render: (val, item) => {
        return <EuiTextColor>{item.get('goods').get('name')}</EuiTextColor>;
      }
    },
    {
      name: '',
      width: '100px',
      field: 'goods.itemCode',
      render: (val, item) => {
        return <EuiTextColor>{item.get('goods').get('itemCode')}</EuiTextColor>;
      }
    },
    {
      name: '',
      field: 'color',
      width: '120px',
      render: (val, item) => {
        return (
          <div className={styles.tableCol}>
            <EuiSuperSelect
              hasDividers
              fullWidth
              disabled
              valueOfSelected={item.get('color')}
              options={colorOptions}></EuiSuperSelect>
          </div>
        );
      }
    },
    {
      name: '',
      field: 'size',
      width: '100px',
      render: (val, item) => {
        return (
          <div className={styles.tableCol}>
            <EuiSuperSelect
              hasDividers
              fullWidth
              disabled
              valueOfSelected={item.get('size')}
              options={sizeOptions}></EuiSuperSelect>
          </div>
        );
      }
    },
    {
      name: '',
      field: 'goods.contact',
      width: '250px',
      render: (val, item) => {
        return (
          <EuiTextColor color="subdued">
            {item.get('goods').get('contact')}
          </EuiTextColor>
        );
      }
    },
    {
      name: '',
      field: 'number',
      width: '50px',
      render: (val, item) => {
        return <EuiTextColor>{item.get('number')}</EuiTextColor>;
      }
    },
    {
      name: '',
      field: 'total',
      width: '150px',
      render: (val, item) => {
        return (
          <EuiTextColor color="danger">
            {ac.formatMoney(item.get('total'), '')}
          </EuiTextColor>
        );
      }
    },
    {
      name: '',
      actions: [
        {
          name: 'Alter',
          description: '',
          icon: 'editorUnorderedList',
          color: 'primary',
          type: 'icon',
          onClick: item => {
            setSelectedStockId(item.id);
            setStockDetailVisible(true);
          },
          isPrimary: true
        }
      ]
    }
  ];
  useEffect(() => {
    queryStocks();
    queryDict();
    queryStat();
  }, []);
  return (
    <Fragment>
      <EuiFlexGroup>
        <EuiFlexItem>
          <EuiPanel>
            <EuiStat
              title={ac.formatNumber(statInfo.allNumber)}
              description=""
              textAlign="right"
              isLoading={statLoading}>
              <EuiIcon type="grid" color="warning" />
            </EuiStat>
          </EuiPanel>
        </EuiFlexItem>
        <EuiFlexItem>
          <EuiPanel>
            <EuiStat
              title={ac.formatMoney(statInfo.allTotal, '')}
              description=""
              textAlign="right"
              isLoading={statLoading}>
              <EuiIcon type="bolt" color="danger" />
            </EuiStat>
          </EuiPanel>
        </EuiFlexItem>
      </EuiFlexGroup>
      <EuiFlexGroup justifyContent="flexEnd">
        <EuiFlexItem grow={false}>
          <EuiButton
            onClick={() => props.history.push('/purchase')}
            color="danger"
            fill
            iconType="refresh">
            
          </EuiButton>
        </EuiFlexItem>
        <EuiFlexItem grow={false}>
          <EuiButton
            onClick={() => props.history.push('/sale')}
            color="secondary"
            fill
            iconType="refresh">
            
          </EuiButton>
        </EuiFlexItem>
        <EuiFlexItem grow={false}>
          <EuiFieldSearch
            isClearable
            fullWidth
            onChange={e => {
              const value = e.target.value;
              debouncedOnSearchChange(value);
            }}
            placeholder="/"></EuiFieldSearch>
        </EuiFlexItem>
      </EuiFlexGroup>
      <EuiBasicTable
        columns={columns}
        items={items}
        onChange={onTableChange}
        pagination={pagination}></EuiBasicTable>
      <EuiSpacer size="xl"></EuiSpacer>
      {stockDetailVisible && (
        <StockDetail
          stockId={selectedStockId}
          onClose={() => setStockDetailVisible(false)}></StockDetail>
      )}
    </Fragment>
  );
};

export default Stock;
