import React, { useState, useEffect } from 'react';
import {
  EuiHeader,
  EuiHeaderSection,
  EuiTextColor,
  EuiHeaderLogo,
  EuiHeaderLinks,
  EuiHeaderLink,
  EuiPopover,
  EuiModal,
  EuiModalHeader,
  EuiModalBody,
  EuiModalFooter,
  EuiOverlayMask,
  EuiModalHeaderTitle,
  EuiButton,
  EuiForm,
  EuiFormRow,
  EuiFieldText,
  EuiFieldPassword
} from '@elastic/eui';
import { useLocation, useHistory } from 'react-router-dom';
import schema from 'async-validator';
import AV from 'leancloud-storage';

import styles from './index.module.scss';

const descriptor = {
  username: {
    type: 'string',
    required: 'true',
    message: ''
  },
  password: {
    type: 'string',
    required: true,
    message: ''
  }
};

const validator = new schema(descriptor);

const Topbar = () => {
  const [loginModalVisible, setLoginModalVisible] = useState(false);
  const [loginLoading, setLoginLoading] = useState(false);
  const [isPopoverOpen, setPopover] = useState(false);
  const [errors, setErrors] = useState([]);
  const location = useLocation();
  const history = useHistory();
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const current = AV.User.current();
  const getRouterProps = path => {
    return {
      href: `#${path}`,
      isActive: location.pathname === path
    };
  };
  const onLogin = async () => {
    let nextErrors = [];
    try {
      await validator.validate({
        username,
        password
      });
      setErrors(nextErrors);
    } catch ({ errors }) {
      nextErrors = nextErrors.concat(errors);
      setErrors(nextErrors);
      return;
    }
    setLoginLoading(true);
    try {
      await AV.User.logIn(username, password);
      setLoginLoading(false);
      history.replace('/a/stock');
    } catch (error) {
      setLoginLoading(false);
    }
  };
  const onUserClick = () => {
    if (current) {
      return history.push('/a/stock');
    }
    return setLoginModalVisible(true);
  };
  const errorMessages = errors.map(e => e.message);
  return (
    <EuiHeader className={styles.header}>
      <EuiHeaderSection border="right">
        <EuiHeaderLogo iconType="logoMetrics" href="#/">
          Neptune
        </EuiHeaderLogo>
      </EuiHeaderSection>
      <EuiHeaderLinks>
        <EuiHeaderLink {...getRouterProps('/post')}></EuiHeaderLink>

        <EuiHeaderLink {...getRouterProps('/profile')}></EuiHeaderLink>

        <EuiPopover
          isOpen={isPopoverOpen}
          closePopover={() => setPopover(false)}
          button={
            <EuiHeaderLink
              onClick={() => setPopover(true)}
              iconType="arrowDown"
              iconSide="right">
              
            </EuiHeaderLink>
          }>
          Hi
        </EuiPopover>
      </EuiHeaderLinks>
      <EuiHeaderSection side="right">
        <EuiHeaderLogo iconType="user" onClick={onUserClick}>
          <EuiTextColor component="span" color="subdued">
            {current ? current.getUsername() : ''}
          </EuiTextColor>
        </EuiHeaderLogo>
      </EuiHeaderSection>
      {loginModalVisible && (
        <EuiOverlayMask>
          <EuiModal onClose={() => setLoginModalVisible(false)}>
            <EuiModalHeader>
              <EuiModalHeaderTitle></EuiModalHeaderTitle>
            </EuiModalHeader>

            <EuiModalBody>
              <EuiForm isInvalid={errors.length > 0} error={errorMessages}>
                <EuiFormRow label="" display="columnCompressed">
                  <EuiFieldText
                    icon="user"
                    value={username}
                    onChange={e => setUsername(e.target.value)}></EuiFieldText>
                </EuiFormRow>
                <EuiFormRow label="" display="columnCompressed">
                  <EuiFieldPassword
                    value={password}
                    onChange={e =>
                      setPassword(e.target.value)
                    }></EuiFieldPassword>
                </EuiFormRow>
              </EuiForm>
            </EuiModalBody>
            <EuiModalFooter>
              <EuiButton isLoading={loginLoading} onClick={onLogin} fill>
                
              </EuiButton>
            </EuiModalFooter>
          </EuiModal>
        </EuiOverlayMask>
      )}
    </EuiHeader>
  );
};

export default Topbar;
